<template>
  <div>
    <Navbar>
      <template v-slot:left>
        <div class="icon"><i class="iconfont icon-jiantou"></i></div>
      </template>
      <template v-slot:default> 订单管理 </template>
      <template v-slot:right> </template>
    </Navbar>
    <van-contact-card
      style="margin-top: 45px"
      type="edit"
      :name="
        address.name +
        ' 地址：' +
        address.province +
        address.city +
        address.county +
        address.address
      "
      :tel="address.phone"
      @click="onEdit"
    />
    <div v-for="(item, index) in cartList" :key="index">
      <van-card
        :num="item.num"
        :price="item.goods.price + '.00'"
        :title="item.goods.title"
        :thumb="item.goods.cover_url"
      />
    </div>
    <van-submit-bar
      style="margin-bottom: 50px"
      :price="total * 100"
      button-text="生成订单"
      @click="handleCreatOrder"
    />
  </div>
  <van-popup
    van-popup
    closeable
    v-model:show="show"
    position="bottom"
    :style="{ height: '30%' }"
    @close="close"
    ><van-grid :border="false" :column-num="1">
      <van-grid-item>
        支付宝<br />
        <van-image width="150px" height="150px" :src="aliyun" />
      </van-grid-item> </van-grid
  ></van-popup>
</template>

<script>
import {
  createOrder,
  getOrderPreview,
  payOrder,
  payOrderStatus,
  payOrderList,
  payOrderDetail,
  confirmOrder,
  viewExpress,
} from "network/orders";
import Navbar from "components/common/navbar/Navbar";
import { Toast } from "vant";
import { useRoute, useRouter } from "vue-router";
import { computed, onMounted, reactive, toRefs } from "vue";
import { useStore } from "vuex";

export default {
  setup() {
    const router = useRouter();
    const route = useRoute();
    const store = useStore();

    const state = reactive({
      cartList: [],
      address: {},
      orderNo: "",
      aliyun: "",
      show: false,
    });

    const init = () => {
      Toast.loading({ message: "加载中...", forbidClick: true });

      getOrderPreview().then((res) => {
        let address = res.address.filter((n) => n.is_default == "1");
        if (address.length == 0) {
          state.address = {
            address: "没有默认地址，请选择或填写地址",
          };
        } else {
          state.address = address[0];
        }

        state.cartList = res.carts;
      });
    };

    onMounted(() => {
      init();
    });

    const total = computed(() => {
      let sum = 0;
      state.cartList.forEach((item) => {
        sum += parseInt(item.num) * parseFloat(item.goods.price);
      });
      return sum;
    });

    const handleCreatOrder = () => {
      const params = {
        address_id: state.address.id,
      };

      createOrder(params).then((res) => {
        Toast("创建订单成功");
        store.dispatch("updataCart");
        state.show = true;
        state.orderNo = res.id;

        payOrder(state.orderNo, { type: "aliyun" }).then((res) => {
          state.aliyun = res.qr_code_url;
        });

        const timer = setInterval(() => {
          payOrderStatus(state.orderNo).then((res) => {
            if (res == "2") {
              clearInterval(timer);
              router.push({
                path: "/orderdetail",
                query: { id: state.orderNo },
              });
            }
          });
        }, 2000);
      });
    };

    const close = () => {
      router.push({ path: "/orderdetail", query: { id: state.orderNo } });
    };

    const onEdit = () => {
      router.push({ path: "/address" });
    };

    return {
      ...toRefs(state),
      onEdit,
      total,
      handleCreatOrder,
      close,
    };
  },

  components: {
    Navbar,
  },
};
</script>

